<script>
import {reactive,onMounted,ref} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "LivePlay",
  setup(){
    const titleQuestion=ref(14)
    const size=ref(16)
    const titleSize=ref(19)
    const list=reactive([])
    const showLive=onMounted(()=>{
      EndPoints.getMatchLive()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      showLive,
      size,
      titleSize,
      titleQuestion,
    }
  }
}
</script>

